import React, {Component} from 'react';
import './index.less';

export default class OrderListItem extends Component {
	constructor(props) {
		super(props);
		
		// 
		this.state = {
			commentState: props.data.commentState
		}
	}
	
	render() {
		let {id, img, title, commentState, count, price} = this.props.data;
		return (
			<div>
				
				<div className="order-list-item">
					<img src={img} alt=""/>
					<div className="list-content">
						<h4>{title}</h4>
						<span>:{count}</span>
						<span>:{price}</span>
					</div>
					<div className="order-button">
						{/*commentState:0() 1() 2()*/}
						{
							this.state.commentState === 0 ?
								<button onClick={this.changeCommentState.bind(this)}></button>
								:
								(this.state.commentState === 1 ?
									'' :
									<button className="unselect"></button>)
						}
					</div>
				</div>
				
				{/**/}
				{
					this.state.commentState == 1 ?
						<div className="comment-area">
							<textarea ref={ref=>this.comment=ref}></textarea>
							<div>
								<button onClick={this.sureCommit.bind(this)}></button>
								<button onClick={this.cancel.bind(this)}></button>
							</div>
						</div>
						: ''
				}
			</div>
		)
	}
	
	changeCommentState() {
		this.setState({  //
			commentState: 1
		})
	}
	
	cancel() {
		this.setState({  //
			commentState: 0
		})
	}
	
	changeState(){
		this.setState({  //
			commentState: 2
		})
	}
	
	sureCommit(){ //
		this.props.commitComment(this.props.data.id,this.comment.value,this.changeState.bind(this));
	}
}


