import React, {Component} from 'react';
import RouterMap from '../routes/index'
import {connect} from 'react-redux';

//
import {bindActionCreators} from 'redux'; //ActionCreator
import * as Actions from '../actions/userInfo'; //actionCreator

import {getStorage} from '../local';

class App extends Component {
	constructor() {
		super();
		this.state = {
			done: false
		}
	}
	
	render() {
		return (
			<div>
				{/*/ RouterMapHome */}
				{
					this.state.done ? <RouterMap/> : <div></div>
				}
			</div>
		)
	}
	
	componentDidMount() {
		//localstorage,cityName
		//1., 
		//2.redux
		let cityName = getStorage('cityName');
		if(cityName == null){
			cityName = '';
		}
		
		//
		this.props.userActions.update({
			cityName:cityName
		});
		
		this.setState({
			done: true
		});
		
	}
}

//(state)(action)App
export default connect(
	state=>{  //mapStateToProps   state
		return {}
	},
	dispatch=>{  //mapDispatchToProps  
		return {
			userActions:bindActionCreators(Actions,dispatch)
		}
	}
)(App);


